// PREVIEW SECTION
.loader{
    display: flex;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #fff;
    opacity: 1;
    z-index: 5;
    transition: all 0.5s ease-out;
}

.loader--fadeOut{
    opacity: 0;
}

.loader--hide{
    display: none;
}

.loader__icon{
    margin: auto;
    background-color: #000;
    content: '';
    width: 30px;
    height: 30px;
    border: 4px solid #000;
    position: relative;
    animation: loader 2s infinite ease;

    &::after{
        content: "";
        position: absolute;
        bottom: 0%;
        right: 0;
        width: 100%;
        left: 0;
        background-color: #fff;
        animation: loader-inner 1s infinite ease-in;
    }
}

@keyframes loader {
    0% {
      transform: rotate(0deg);
    }
    
    25% {
      transform: rotate(180deg);
    }
    
    50% {
      transform: rotate(180deg);
    }
    
    75% {
      transform: rotate(360deg);
    }
    
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes loader-inner {
    0% {
      height: 0%;
    }
    
    25% {
      height: 0%;
    }
    
    50% {
      height: 100%;
    }
    
    75% {
      height: 100%;
    }
    
    100% {
      height: 0%;
    }
  }

